/*===== global styles =====*/
body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  margin: 0;
}

.block_preim_krug_art6 {
  padding: 31px 0 33px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  background-color: #fff;
}
.block_preim_krug_art6 *,
.block_preim_krug_art6 *::before,
.block_preim_krug_art6 *::after {
  box-sizing: border-box;
}
@media (max-width: 767px) {
  .block_preim_krug_art6 {
    padding: 24px 0 42px;
  }
}

.block_preim_krug_art6__container {
  position: relative;
  width: 100%;
  max-width: 1580px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 40px;
  display: flex;
  justify-content: center;
}
@media (max-width: 767px) {
  .block_preim_krug_art6__container {
    padding: 0 15px;
  }
}
.block_preim_krug_art6__container::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  transform: translate(-50%, -50%);
  width: 96%;
  max-width: 1156px;
  aspect-ratio: 1156/267;
  background-color: #f2f6ec;
  border-radius: 50% 50%;
}
@media (max-width: 991px) {
  .block_preim_krug_art6__container::after {
    aspect-ratio: 1156/410;
  }
}
@media (max-width: 767px) {
  .block_preim_krug_art6__container {
    justify-content: flex-start;
  }
  .block_preim_krug_art6__container::after {
    display: none;
  }
}

.block_preim_krug_art6__list {
  position: relative;
  z-index: 2;
  display: grid;
  grid-template-columns: repeat(3, 206px);
  gap: 35px 110px;
}
@media (max-width: 991px) {
  .block_preim_krug_art6__list {
    gap: 25px 50px;
  }
}
@media (max-width: 767px) {
  .block_preim_krug_art6__list {
    grid-template-columns: 1fr;
    gap: 20px 0;
  }
}

.block_preim_krug_art6__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}
@media (max-width: 767px) {
  .block_preim_krug_art6__item {
    flex-direction: row;
    gap: 0 26px;
  }
}

.block_preim_krug_art6__icon {
  flex: 0 0 69px;
  display: block;
  width: 69px;
  height: 69px;
  object-fit: contain;
}

.block_preim_krug_art6__descr {
  font-weight: 400;
  font-size: 20px;
  text-align: center;
  color: #000;
}
@media (max-width: 767px) {
  .block_preim_krug_art6__descr {
    font-size: 16px;
    text-align: left;
  }
}

.block_konsult_art6 {
  padding: 48px 0;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  background-color: #fff;
}
.block_konsult_art6 *,
.block_konsult_art6 *::before,
.block_konsult_art6 *::after {
  box-sizing: border-box;
}
@media (max-width: 991px) {
  .block_konsult_art6 {
    overflow: auto;
  }
}
@media (max-width: 767px) {
  .block_konsult_art6 {
    padding: 36px 0 85px;
  }
}

.block_konsult_art6__container {
  position: relative;
  width: 100%;
  max-width: 1580px;
  min-width: 320px;
  min-height: 730px;
  margin: 0 auto;
  padding: 0 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 991px) {
  .block_konsult_art6__container {
    display: block;
  }
}
@media (max-width: 767px) {
  .block_konsult_art6__container {
    padding: 0 15px;
  }
}
.block_konsult_art6__container::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  transform: translate(-50%, -50%);
  width: 503px;
  aspect-ratio: 1/1;
  border-radius: 50%;
  border: 3px solid #7da543;
}
@media (max-width: 1200px) {
  .block_konsult_art6__container::after {
    width: 400px;
  }
}
@media (max-width: 991px) {
  .block_konsult_art6__container::after {
    display: none;
  }
}
.block_konsult_art6__container::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  transform: translate(-50%, -50%);
  width: 293px;
  aspect-ratio: 293/268;
  border-radius: 50%;
  background: url("../images/block_konsult_art6/planet.svg") center/contain no-repeat;
}
@media (max-width: 1200px) {
  .block_konsult_art6__container::before {
    width: 250px;
    transform: translate(-48%, -52%);
  }
}
@media (max-width: 991px) {
  .block_konsult_art6__container::before {
    display: none;
  }
}

.block_konsult_art6__title {
  position: relative;
  z-index: 2;
  max-width: 278px;
  margin: 0;
  font-weight: 700;
  font-size: 20px;
  line-height: 110%;
  text-align: center;
  color: #000;
  text-shadow: -2px 4px 6px 0 #fff, 0 -3px 9px 0 #fff;
}
.block_konsult_art6__title span {
  color: #446b0c;
}
.block_konsult_art6__title sup {
  font-size: 12px;
}
@media (max-width: 991px) {
